import React, { Component } from 'react'

import '../../Styles/About.css'
import mongoLogo from '../../Assets/Images/MongoSmallWhite.png'
import nodeLogo from '../../Assets/Images/NodeSmallWhite.png'
import reactLogo from '../../Assets/Images/ReactSmallWhite.png'
import reduxLogo from '../../Assets/Images/ReduxSmallWhite.png'


class About extends Component {
    render() {
        return(
            <div className="about-section" id="about">

                <div className="about-me-container">
                    <p style={{textAlign: 'justify'}}>
                        <span style={{fontSize: '35pt'}}><strong><span style={{color: '#92cdcf'}}>&lt;<span style={{fontSize: '15pt'}}>&nbsp;</span></span></strong><span style={{fontSize: '28pt'}}>ABOUT ME</span></span><br /><span style={{fontSize: '12pt'}}>&nbsp; &nbsp; &nbsp;<span>name="Dan Craycraft"</span></span><br /><span>&nbsp; &nbsp; &nbsp;label="Front-End Developer"</span><br />
                        <span>&nbsp; &nbsp; &nbsp;type="JavaScript"</span><br /><span><span style={{verticalAlign: 'top'}}>&nbsp; &nbsp; &nbsp;onClick={'{'} this.developThings {'}'}</span> <br />
                        <strong><span style={{fontSize: '32pt', color: '#92cdcf'}}>/&gt;</span></strong></span>
                    </p>
                </div>
                <div className="skills-container">
                    <p><span style={{fontSize: '32pt'}}>SKILLS</span> <strong><span style={{color: '#92cdcf', fontSize: '24pt'}}>= {'{'}</span></strong></p>
                    <p><span>&nbsp; &nbsp;front-end: [ ES6 JS React Redux ]</span></p>
                    <p><span>&nbsp; &nbsp;back-end: [ Node Express MongoDB ]</span></p>
                    <p><span>&nbsp; &nbsp;ui-design: [ Photoshop Adobe XD ]</span></p>
                    <p><span style={{color: '#92cdcf', fontSize: '28pt'}}><strong>{'}'}</strong></span></p>
                </div>

                <div className="logo-container">
                    <img src={mongoLogo} alt='' className="logo"/>
                    <img src={nodeLogo} alt='' className="logo" style={{height: '80px'}}/>
                    <img src={reactLogo} alt='' className="logo"/>
                    <img src={reduxLogo} alt='' className="logo"/>
                </div>

            </div>
        )
    }
}

export default About


